<template>
  <div class="basicInfo" style="top:3.9vh;">
    <div class="Btitle">
      <span class="basicData"></span>
      <span class="saveBtn mainBtn" @click="saveUp" v-show="saveBtnBl"></span>
    </div>
    <div class="basic">
      <div class="subTitle"></div>
      <ul>
        <li>
          <span class="listTitle">:</span>
          <span class="margin-l" v-if="!userNameBl">{{ userName }}</span>
          <input class="listInput margin-l" v-model="userName" v-if="userNameBl" />
          <span
            class="icon biFont icon-edit edit"
            @click="userNameBl = !userNameBl"
            v-if="!userNameBl"
          ></span>
        </li>
        <li>
          <span class="listTitle">
            <span class="mandatory">*</span>:
          </span>
          <input class="listInput margin-l" v-model="firmName" />
        </li>
        <li>
          <span class="listTitle">
            <span class="mandatory">*</span>:
          </span>
          <Selection
            class="selections margin-l"
            @selectValue="mainIndustryClick"
            :selections="mainIndustryObj"
            :sonValue="mainIndustryStr"
          ></Selection>
        </li>
        <li>
          <span class="listTitle">
            <span class="mandatory">&nbsp;&nbsp;</span>&nbsp;&nbsp;&nbsp;
          </span>
          <Selection
            class="selections margin-l"
            @selectValue="industryClick"
            :selections="industryObj"
            :textBl="industryBl"
            :sonValue="industryStr"
          ></Selection>
        </li>
        <li>
          <span class="listTitle">:</span>
          <input class="listInput margin-l" v-model="business" />
        </li>
      </ul>
    </div>
    <div class="contact">
      <div class="subTitle"></div>
      <ul>
        <li>
          <span class="listTitle">/:</span>
          <Selection
            class="selections margin-l"
            @selectValue="countryClick"
            :selections="countryObj"
            :sonValue="countryVal"
          ></Selection>
        </li>
        <li>
          <span class="listTitle">:</span>
          <Selection
            class="selections margin-l"
            @selectValue="provinceClick"
            :selections="provinceObj"
            :textBl="provinceBl"
            :sonValue="provinceVal"
          ></Selection>
        </li>
        <li>
          <span class="listTitle">:</span>
          <Selection
            class="selections margin-l"
            @selectValue="cityClick"
            :selections="cityObj"
            :textBl="cityBl"
            :sonValue="cityVal"
          ></Selection>
        </li>
        <li>
          <span class="listTitle">:</span>
          <input class="listInput margin-l" v-model="address" />
        </li>
        <li>
          <span class="listTitle">:</span>
          <span class="margin-l">{{ phone }}</span>
          <router-link
            :to="{ name: 'securitySetting' }"
            class="tipsBtn securityBtn"
            v-text="phoneBl?'':''"
          ></router-link>
        </li>
        <li>
          <span class="listTitle">:</span>
          <span class="margin-l">{{ eMail }}</span>
          <router-link
            :to="{ name: 'securitySetting' }"
            class="tipsBtn securityBtn"
            v-text="eMailBl?'':''"
          ></router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
// 
import globalName from "@/assets/UserCenter/js/globalName";
// 
import NationalIndustryArr from "@/assets/UserCenter/js/NationalIndustryArr";
import Selection from "@/pages/UserCenter/UserItem/Selection.vue";
export default {
  name: "BasicInfo",
  components: { Selection },
  data() {
    return {
      mobileData: [],
      // 
      saveBtnBl: false,
      saveBtnBlArr: [false, false],
      // ajax
      ajaxDataObj: {
        phone: "",
        email: "",
        userName: "",
        address: "",
        countryVal: "",
        provinceVal: "",
        cityVal: "",
        firmName: "",
        mainIndustryStr: "",
        industryStr: "",
        business: ""
      },
      // input
      userNameBl: false,
      // 
      globalName: globalName.obj.data,
      // 
      countryVal: "",
      // countryObj.data
      countryArr: [],
      // obj
      countryObj: {
        placeholder: "",
        inputVal: "",
        w: "16vw",
        data: ""
      },
      // val
      provinceBl: false,
      // 
      provinceVal: "",
      // 
      provinceArr: [],
      // obj
      provinceObj: {
        placeholder: "",
        w: "16vw",
        data: ""
      },
      // val
      cityBl: false,
      // 
      cityVal: "",
      // 
      cityArr: [],
      // obj
      cityObj: {
        placeholder: "",
        w: "16vw",
        data: ""
      },
      // 
      NationalIndustryArr: NationalIndustryArr.NationalIndustryArr,
      // Obj
      mainIndustryObj: {
        placeholder: "",
        inputVal: "",
        w: "16vw",
        data: ""
      },
      // 
      mainIndustryStr: "",
      // obj
      industryObj: {
        placeholder: "",
        inputVal: "",
        w: "16vw",
        data: ""
      },
      // 
      industryStr: "",
      // value
      industryBl: false,
      userName: "",
      firmName: "",
      password: "",
      // 
      firmIndustry: "",
      // 
      business: "",
      // 
      address: "",
      // 
      phone: "",
      // Bl
      phoneBl: false,
      // 
      eMail: "",
      // bl
      eMailBl: false
    };
  },
  methods: {
    init() {
      this.$get("api/basicsData").then(res => {
        if (res.code === "000000") {
          this.mobileData = res.data;
          this.ajaxDataObj.userName = res.data.nickName;
          this.userName = res.data.nickName;
          this.ajaxDataObj.firmName = res.data.firmName;
          this.firmName = res.data.firmName;
          this.ajaxDataObj.mainIndustryStr = res.data.industry;
          this.mainIndustryStr = res.data.industry;
          this.ajaxDataObj.industryStr = res.data.sonIndustry;
          this.industryStr = res.data.sonIndustry;
          this.ajaxDataObj.business = res.data.business;
          this.business = res.data.business;
          this.ajaxDataObj.address = res.data.location;
          this.address = res.data.location;
          this.ajaxDataObj.eMail = res.data.email;
          this.eMail = res.data.email;
          this.ajaxDataObj.eMail = res.data.phone;
          this.ajaxDataObj.countryVal = res.data.country;
          this.countryVal = res.data.country;
          this.ajaxDataObj.provinceVal = res.data.province;
          this.provinceVal = res.data.province;
          this.ajaxDataObj.cityVal = res.data.city;
          this.cityVal = res.data.city;
          this.phone = res.data.phone;
          this.saveBtnBl = false;
          this.saveBtnBlArr[0] = true;
          this.saveBtnBlArr[1] = true;
          const data = {
            nickName: res.data.nickName,
            iconUrl: localStorage.getItem("headimgurl")
          };
          this.$store.commit("userCenter/setUserInfo", data);
        }
      });
    },
    // 
    fn() {
      // 
      for (let i = 0; i < this.globalName.length; i++) {
        this.countryArr.push({ label: this.globalName[i].name, value: i });
      }
      this.countryObj.data = this.countryArr;
      // 
      var mainIndustryArr = [];
      for (let j = 0; j < this.NationalIndustryArr.length; j++) {
        mainIndustryArr.push({
          label: this.NationalIndustryArr[j].name,
          value: j
        });
      }
      this.mainIndustryObj.data = mainIndustryArr;
    },
    getByteLen(str, maxNum, minNum) {
      var len = 0;
      if (str) {
        for (var i = 0; i < str.length; i++) {
          var a = str.charAt(i);
          if (a.match(/[^\x00-\xff]/gi) != null) {
            len += 2;
          } else {
            len += 1;
          }
        }
        if (maxNum) {
          if (len > maxNum) {
            return true;
          }
        } else if (minNum) {
          if (len < minNum) {
            return true;
          }
        }
        return false;
      } else {
        return true;
      }
    },
    // 
    saveUp() {
      // truefalse
      if (this.countryVal == "") {
        this.tips.show([""]);
        return;
      }
      if (this.provinceVal == "") {
        this.tips.show([""]);
        return;
      }
      if (this.cityVal == "") {
        this.tips.show([""]);

        return;
      }
      if (this.mainIndustryStr == "") {
        this.tips.show([""]);
        return;
      }
      if (this.industryStr == "") {
        this.tips.show([""]);
        return;
      }
      if (this.userName == "" || this.getByteLen(this.userName, 16)) {
        this.tips.show(["16"]);
        return;
      }
      if (this.firmName == "" || this.getByteLen(this.firmName, 30)) {
        this.tips.show(["30"]);
        return;
      }
      if (this.business == "" || this.getByteLen(this.business, 50)) {
        this.tips.show(["50"]);
        return;
      }
      if (this.address == "" || this.getByteLen(this.address, 50)) {
        this.tips.show(["50"]);
        return;
      }
      this.$put("api/basicsData", {
        nickName: this.userName,
        firmName: this.firmName,
        industry: this.mainIndustryStr,
        sonIndustry: this.industryStr,
        business: this.business,
        country: this.countryVal,
        province: this.provinceVal,
        city: this.cityVal,
        location: this.address
      }).then(res => {
        if (res.code == "000000") {
          this.tips.show(["", true]);
          this.userNameBl = false;
          this.init();
        } else {
          this.tips.show(["", false]);
        }
      });
    },
    // 
    countryClick(valueObj) {
      // 
      if (this.countryVal != "" && this.countryVal != valueObj.label) {
        // --
        this.provinceBl = true;
        // 
        this.cityBl = true;
        // 
        this.provinceVal = "";
        this.cityVal = "";
      }
      // 
      this.provinceObj.data = [];
      // 
      this.provinceArr = [];
      // 
      this.cityArr = [];
      // 
      this.countryVal = valueObj.label;
      for (let i = 0; i < this.globalName.length; i++) {
        // 
        if (
          valueObj.label == this.globalName[i].name &&
          this.globalName[i].clist
        ) {
          // push
          for (let j = 0; j < this.globalName[i].clist.length; j++) {
            this.provinceArr.push({
              label: this.globalName[i].clist[j].name,
              value: j
            });
          }
          // 
        } else if (
          valueObj.label == this.globalName[i].name &&
          this.globalName[i].clist == null
        ) {
          // 
          this.provinceArr = [
            {
              label: this.globalName[i].name,
              value: i
            }
          ];
          this.cityArr = [
            {
              label: this.globalName[i].name,
              value: i
            }
          ];
        }
      }
      // 
      this.provinceObj.data = this.provinceArr;
      this.cityObj.data = this.cityArr;
    },
    // 
    provinceClick(valueObj) {
      // 
      this.provinceBl = false;
      // 
      if (this.provinceVal != "" && this.provinceVal != valueObj.label) {
        // 
        this.cityBl = true;
        this.cityVal = "";
      }
      // 
      this.provinceVal = valueObj.label;
      // 
      this.cityArr = [];
      // 
      for (let i = 0; i < this.globalName.length; i++) {
        // 
        if (
          this.countryVal == this.globalName[i].name &&
          this.globalName[i].clist
        ) {
          // 
          for (let j = 0; j < this.globalName[i].clist.length; j++) {
            // 
            if (this.provinceVal == this.globalName[i].clist[j].name) {
              // 
              for (
                let n = 0;
                n < this.globalName[i].clist[j].pchilds.length;
                n++
              ) {
                this.cityArr.push({
                  label: this.globalName[i].clist[j].pchilds[n].name,
                  value: n
                });
              }
            }
          }
          // 
        } else if (
          valueObj.label == this.globalName[i].name &&
          this.globalName[i].clist == null
        ) {
          this.provinceArr = [
            {
              label: this.globalName[i].name,
              value: i
            }
          ];
          this.cityArr = [
            {
              label: this.globalName[i].name,
              value: i
            }
          ];
        }
      }
      // 
      this.provinceObj.data = this.provinceArr;
      this.cityObj.data = this.cityArr;
    },
    // 
    cityClick(valueObj) {
      // 
      this.cityBl = false;
      this.cityVal = valueObj.label;
    },
    // 
    mainIndustryClick(valueObj) {
      // --
      if (
        this.mainIndustryStr != "" &&
        this.mainIndustryStr != valueObj.label
      ) {
        this.industryBl = true;
        this.industryStr = "";
      }
      // 
      var mainIndustryArr = [];
      // 
      this.mainIndustryStr = valueObj.label;
      // 
      for (let i = 0; i < this.NationalIndustryArr.length; i++) {
        if (valueObj.label == this.NationalIndustryArr[i].name) {
          for (
            let j = 0;
            j < this.NationalIndustryArr[i].childrenArr.length;
            j++
          ) {
            mainIndustryArr.push({
              label: this.NationalIndustryArr[i].childrenArr[j],
              value: j
            });
          }
        }
      }
      // 
      this.industryObj.data = mainIndustryArr;
    },
    // 
    industryClick(valueObj) {
      // false
      this.industryBl = false;
      // 
      this.industryStr = valueObj.label;
    }
  },
  mounted() {
    this.fn();
    this.init();
  },
  watch: {
    phone(newVal, oldVal) {
      if (
        this.ajaxDataObj.phone != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    email(newVal, oldVal) {
      if (
        this.ajaxDataObj.email != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    userName(newVal, oldVal) {
      if (
        this.ajaxDataObj.userName != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    address(newVal, oldVal) {
      if (
        this.ajaxDataObj.address != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    countryVal(newVal, oldVal) {
      if (
        this.ajaxDataObj.countryVal != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    provinceVal(newVal, oldVal) {
      if (
        this.ajaxDataObj.provinceVal != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    cityVal(newVal, oldVal) {
      if (
        this.ajaxDataObj.cityVal != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    firmName(newVal, oldVal) {
      if (
        this.ajaxDataObj.firmName != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    mainIndustryStr(newVal, oldVal) {
      if (
        this.ajaxDataObj.mainIndustryStr != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    industryStr(newVal, oldVal) {
      if (
        this.ajaxDataObj.industryStr != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    },
    business(newVal, oldVal) {
      if (
        this.ajaxDataObj.business != newVal &&
        this.saveBtnBlArr[0] &&
        this.saveBtnBlArr[1]
      ) {
        this.saveBtnBl = true;
      } else {
        this.saveBtnBl = false;
      }
    }
  },
  computed: {
    ...mapGetters("userCenter", {
      userInfo: "getUserInfo"
    })
  }
};
</script>

<style scoped>
.basicInfo .Btitle {
  height: 4.5vh;
  width: 83.8vw;
}
.basicInfo .Btitle .basicData {
  border-left: solid #28b3fc 0.0625vw;
  padding-left: 0.5vw;
  font-size: 0.9vw;
  line-height: 4vh;
}
.Btitle span {
  position: relative;
  top: -0.1vh;
}
.basicInfo .Btitle .saveBtn {
  padding: 0.4vh 0.4vw;
  margin-left: 2vw;
  font-size: 0.8vw;
  height: 3.5vh;
  width: 5vw;
  line-height: 2.8vh;
  text-align: center;
  display: inline-block;
  background-color: #283247;
  border: solid 0.0625vw #283247;
  margin-top: 0.07vw;
  top: 0;
}
.basicInfo .Btitle .saveBtn:hover {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}

.basicInfo .subTitle {
  font-size: 0.9vw;
  padding: 1.3vh 0 1.3vh 1.3vw;
  border-bottom: solid 0.0625vw #0e121a;
}

.basicInfo .basic,
.basicInfo .contact {
  background-color: #1c2433;
  width: 83.8vw;
  margin-top: 1.8vh;
}

.basicInfo .basic > ul,
.basicInfo .contact > ul {
  font-size: 0.8vw;
  padding: 3vh 0 1vh 1.3vw;
}

.basicInfo .basic > ul > li,
.basicInfo .contact > ul > li {
  line-height: 4.5vh;
}

.basicInfo .basic .edit {
  font-size: 0.6vw;
  margin-left: 0.2vw;
  cursor: pointer;
  color: #28b3fc;
  vertical-align: 0;
}

.basicInfo .basic .mandatory {
  color: #ff2929;
  /* font-size: 1.2vw; */
  text-align: center;
}

.basicInfo .selections .selection-show .triangle {
  top: 2vh;
}

.basicInfo .selections {
  display: inline-block;
}

.basicInfo .listTitle {
  width: 5em;
  height: 2.5vw;
  display: inline-block;
  text-align: right;
}

.basicInfo .listInput {
  width: 16vw;
  height: 2.8vh;
  font-size: 0.7vw;
  color: #fff;
  background-color: #121721;
  border: solid 0.0625vw #475566;
  padding-left: 0.2vw;
}

.basicInfo .margin-l {
  margin-left: 0.5vw;
}

.securityBtn {
  font-size: 0.6vw;
  line-height: 1.9vh;
  padding: 0.4vh 0.6vw;
  cursor: pointer;
  background-color: #283247;
  border: solid 0.0625vw #283247;
  margin: 0.5vh 0 0 1vw;
}
.securityBtn:hover {
  background-color: #1d4c6c;
  border: solid 0.0625vw #00b9ff;
}
.listInput:focus {
  border: solid 0.0625vw #28b3fc;
}
</style>
